@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
/* TODO Think about this */
::-webkit-scrollbar {
  display: none; }

* {
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box; }

body {
  margin: 0; }

p, li {
  color: #28404d;
  font-size: 1.2em;
  font-weight: bold; }

.text-color-pink {
  color: #b0003a; }

.syntax-hl {
  margin: auto;
  width: max-content;
  border-radius: 5px; }

.attribute-box {
  display: flex;
  flex-direction: row;
  justify-content: center; }

.attribute-text {
  margin: 0 1em 0 1em;
  height: fit-content;
  width: fit-content;
  max-width: 25%; }

.content-text {
  width: 35%;
  min-width: 325px;
  height: fit-content;
  margin: auto; }

@media screen and (max-width: 425px) {
  .attribute-box {
    flex-direction: column; }
  .attribute-text {
    max-width: none; } }
